import React from 'react';

// тут весь прикол в том, что error возвращается как объект в любом случае. И он пустой, но все равно не undefined.
// разберись с валедаторами и после если объект пустой, то возвращай undefined. И для каждого поля сделай свой валидатор.
// так же подумай над отображением ошибок. А вообще, если на кождое поле свой валидатор, то возвращать нужно одну ошибку.
// Сделать кучу (необходимую) проверок и при попадании первой ошибки возвращать её (первую попавшуюся)!!!

//Вариант с проверкой паролей: в стэйте создать переменную, которая отвечает за совпадение паролей при регистрации.
//Далее при submit на странице регистрации проверять эквивалентность паролей. Если нет возвращать на страницу и по значению параметра
// выводить надпись о несовпадении паролей. какой нибудь isPasswordEqual. При клике на это поле убирать надпись (display: none, через hook). В state по клику лезть не надо
//в state что то менять только при сабмите.

export const emailValidate = values => {
    switch (true) {
        case (!values):
            return 'Required';
        case (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values)):
            return 'Invalid email address';
        default:
            return undefined
    }
};

export const passwordValidate = values => {
    switch (true) {
        case (!values):
            return 'Required';
        case (!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/i.test(values)):
            return 'Must contain at least one number and one uppercase and lowercase letter, and at least 8 or more characters';
        default:
            return undefined
    }
};

export const telValidate = values => {
    switch (true) {
        case (!values):
            return 'Required';
        case (!/^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/i.test(values)):
            return 'Enter correct telephone number';
        default:
            return undefined
    }
};

export const validate = values => {
    const errors = {};

    if (!values.email) {
        errors.email = 'Required'
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'Invalid email address'
    }

    if (!values.password) {
        errors.password = 'Required'
    } else if (!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/i.test(values.password)) {
        errors.password = 'Must contain at least one number and one uppercase and lowercase letter, and at least 8 or more characters'
    }

    if (!values.repeatPassword) {
        errors.repeatPassword = 'Required'
    } else if ((!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/i.test(values.repeatPassword))) {
        errors.repeatPassword = 'Must contain at least one number and one uppercase and lowercase letter, and at least 8 or more characters'
    } else if ((values.password !== values.repeatPassword)) {
        errors.repeatPassword = 'Passwords must be equals'
    }

    if (!values.tel) {
        errors.tel = 'Required'
    } else if ((!/^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7}$/i.test(values.tel))) {
        errors.tel = 'Enter correct telephone number';
    }

    if(!values.agree){
        errors.agree = 'Must agree'
    }

    return errors
};