import React, {useEffect} from 'react';
import style from './Registration.module.css';
import ReduxRegistrationForm from "./RegistrationForm";
import {connect} from "react-redux";
import {checkAgreementChecked, checkPasswordsMatching} from "../../redux/reducers/registration-reducer";

const Registration = ({hideHeader, showHeader, passwords_are_equals, checkPasswordsMatching, isAgreementChecked, checkAgreementChecked}) => {

    console.log("passwords_are_equals " + passwords_are_equals);

// если пользователь авторизован, то сюда он никак не дожен попасть!!!!!

    useEffect(() => {
        hideHeader();
        return () => {
            showHeader();
        };
    },);

    const onSubmit = (formData) => {
        console.log("Submit");
        console.log(formData);
        checkPasswordsMatching(formData.password, formData.repeatPassword);
        checkAgreementChecked(formData.agree);
    };


    return (<div className={`text-center ${style.registrationPage}`}>

            <img className="mb-5" src="5551.png" alt="" width="300px" height="100px"/>
            <ReduxRegistrationForm onSubmit={onSubmit} passwords_are_equals={passwords_are_equals}
                                   isAgreementChecked={isAgreementChecked}/>
            <p className="mt-5 mb-3 text-muted">@VPROFI.RU 2019 All rights reserved </p>

        </div>
    )
};

const mapStateToProps = (state) => ({
    passwords_are_equals: state.registrationPage.passwords_are_equals,
    isAgreementChecked: state.registrationPage.isAgreementChecked
});

export default connect(mapStateToProps, {checkPasswordsMatching, checkAgreementChecked})(Registration);